import { HttpClient, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable, of, switchMap, tap, throwError } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class LandingService {

  private url: string = `${environment.urlWeb}`;
  private _manillaInfo: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  /**
   * Constructor
   */
  constructor(private _httpClient: HttpClient) {}

   /**
   * Getter for manilla
   */
   get manillaInfo$(): Observable<any[]> {
    return this._manillaInfo.asObservable();
  }
  
  getFieldsByUserTypes(type:any): Observable<any>{
    let params = new HttpParams();
    params = params.set('type', type);
    return this._httpClient.get<any[]>(`${this.url}authentication/fields`, {params});
}

  getManillaInfo(id:any): Observable<any> {
    return this._httpClient.get<any[]>(`${this.url}manillas/findById/${id}`).pipe(
        tap((manilla: any) => {
          this._manillaInfo.next(manilla);

          return manilla
        }),
        switchMap((manilla) => {
            if ( !manilla )
            {
                return throwError('No se pudo encontrar la manilla con el id: ' + id + '!');
            }

            return of(manilla);
        })
      );
  }
}
