import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { LandingService } from '../landing.service';
import { Subject, takeUntil } from 'rxjs';
import { Lightbox } from 'ng-gallery/lightbox';
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';

@Component({
  selector: 'app-user-info',
  templateUrl: './user-info.component.html',
  styleUrls: ['./user-info.component.scss']
})
export class UserInfoComponent implements OnInit, OnDestroy{

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  manilla: any = {};

  items: GalleryItem[];

  fieldsList:any = [];

  constructor(private _landingService: LandingService, 
    private _changeDetectorRef: ChangeDetectorRef, public gallery: Gallery, public lightbox: Lightbox){

  }

  ngOnInit(): void {
    //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
    //Add 'implements OnInit' to the class.
    // Subscribe to manilla changes
    this._landingService.manillaInfo$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((manilla: any) => {
        if(manilla.foto_portador){
          this.items = [new ImageItem({ src: manilla.foto_portador, thumb: manilla.foto_portador })];
          // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
          const lightboxRef = this.gallery.ref('lightbox');
          // Add custom gallery config to the lightbox (optional)
          lightboxRef.setConfig({
            imageSize: ImageSize.Cover,
            thumbPosition: ThumbnailsPosition.Top
          });

          // Load items into the lightbox gallery ref
          lightboxRef.load(this.items);
        }

        this.getFieldsList(manilla.tipo);

        this.manilla = manilla;

        // Mark for check
        this._changeDetectorRef.markForCheck();
    });
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  getFieldsList(type:any){
    this._landingService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.fieldsList = response;
      },(error) => {
        console.log(error);
      }
    );
  }
}
